.slidegroup {
  background-color: black;
  position: sticky;
  top: 60px;
  z-index: 498;
}
.menu-bar-ext {
  width: 100vw;
  padding: 10px 10px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  transition: 3s ease-in-out;
  border-radius: 30px 30px 0px 0px;
  background-color: var(--background-color);
}
.menu-bar-ext h1 {
  height: max-content;
  width: 100%;
  padding: 10px;
  text-align: left;
  color: var(--font-color);

  font-size: 22.5px;
}
.menu-bar-ext .menu2 {
  display: flex;
  overflow: scroll;
  width: 100vw;
}
.menu-bar-ext .menu2 a {
  background-color: rgb(187, 187, 187);

  font-weight: 700;
  border-radius: 20px;
  margin: 5px;
  font-size: 15px;
  max-width: 150px;
  height: 40px;
  width: max-content;
  text-decoration: none;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px 25px;
}
