.head-top {
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.barnav {
  background-color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  width: 100vw;
  position: sticky;
  top: 0;
  z-index: 499;
  height: 60px;
}
.barnav button {
  display: flex;
  justify-content: space-around;
  background: transparent;
  border: none;
}
.barnav button img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 5px;
}
.barnav button div h3 {
  font-size: 25px;
  color: white;
  text-align: center;
  text-decoration: none;
  font-weight: 700;
}
.barnav button div h4 {
  color: red;
  font-size: 15px;
  text-align: left;
}
#hora ion-icon {
  color: white;
  width: 40px;
  height: 40px;
}
.SearchBar {
  margin: 10px 5px;
  display: flex;
  height: 40px;
  width: max-content;
  border-radius: 20px;
  background-color: rgb(12, 12, 12);
}
.SearchBar ion-icon {
  display: flex;
  font-size: 30px;
  height: 40px;
  width: 30px;
  margin: 0px 10px;
  color: grey;
  border-radius: 20px 0px 0px 20px;
}
.SearchBar input {
  display: flex;
  height: 40px;
  width: 80vw;
  background-color: rgb(12, 12, 12);
  border: none;
  padding: 0px 10px;
  border-radius: 0px 20px 20px 0px;
  color: aliceblue;
}
.btn-menu-global {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-backdrop-filter: grayscale(1);
  backdrop-filter: grayscale(1);
  background-color: transparent;
  z-index: 600;
}
.sombra {
  height: 50vh;
  width: 100%;
  background-color: transparent;
  border: transparent;
}
.Menu-global {
  height: 50vh;
  width: 100%;
  background-color: var(--background-color);
  border-radius: 50px 50px 0px 0px;
  box-shadow: -1px -20px 20px 20px rgba(0, 0, 0, 0.2);
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.Menu-global .linear {
  width: 100px;
  height: 10px;
  border-radius: 10px;
  background: var(--second-color);
  box-shadow: -3px -1px 1px 0px rgba(0, 0, 0, 0.2);
}
.Menu-global .menu-content {
  height: 40vh;
  width: 100%;
  overflow-y: scroll;
}
