:root {
  --primary-color: #333c9b;
  --background-color: #f9f5f0;
  --second-color: #f5f0f0;
  --font-color: #151515;
  --font2-color: #414040;
}
html {
  font-size: 62.5%;
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
  transition: 1s;
  display: flex;
  justify-content: space-around;
  background-color: var(--background-color);
}
/********/
/*HEADER*/
/********/
.line {
  margin: 1px 0px;
  z-index: 499;
  color: grey;
}
.fav {
  display: flex;
  position: sticky;
  z-index: 399;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  bottom: 10px;
  justify-content: space-around;
  padding: 5px;
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  margin: 0px auto;
  width: 50vw;
  border-radius: 20px;
}

.fav a {
  display: flex;
  padding: 5px 20px;
}
.fav a p {
  height: 15px;
  width: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 22px;
  font-size: 10px;
  font-weight: 700;
  margin: 2px 2px;
  background-color: var(--font-color);
  color: var(--background-color);
}
.fav a ion-icon {
  font-size: 20px;
  color: var(--primary-color);
}
.fav a h2 {
  font-size: 15px;
  margin: auto auto;
  color: var(--font-color);
}
.hov {
  background-color: rgba(168, 168, 168, 0.4);
  border-radius: 10px;
  transition: 1s ease-in-out;
}
.head {
  padding: 10px;
}
.head img {
  width: 100%;
  border-radius: 20px;
  margin-bottom: 5px;
  max-height: max-content;
}
.head h2 {
  font-size: 20px;
  margin-bottom: 10px;
  color: var(--font-color);
}
.head p {
  font-size: 12px;
  color: var(--font-color);
}
.head a {
  font-size: 12px;
  text-decoration: none;
  color: black;
  color: var(--font-color);
}
.container-features {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 3rem;
  padding: 3rem 0;
}
.card-feature {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;

  background-color: #fff;
  border-radius: 1rem;
  padding: 1.5rem 0;
}

.card-feature ion-icon {
  font-size: 2.7rem;
  color: var(--primary-color);
}

.feature-content {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.feature-content span {
  font-weight: 700;
  font-size: 1.2rem;
  color: var(--font-color);
}

.feature-content p {
  color: var(--font2-color);
  font-weight: 500;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  max-width: 120rem;
  margin: 0 auto;
}
.heading-1 {
  text-align: center;
  font-weight: 500;
  font-size: 3rem;
}

.card-feature {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;

  background-color: var(--second-color);
  border-radius: 1rem;
  padding: 1.5rem 0;
}

.card-feature i {
  font-size: 2.7rem;
  color: var(--primary-color);
}
.top-products {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 3rem;
}
.location {
  background: var(--primary-color);
  height: 30px;
  width: 150px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  margin-top: 20px;
  display: flex;
  align-items: center;
  padding-left: 10px;
}
.location ion-icon {
  font-size: 16px;
  color: white;
}
.location a {
  font-size: 15px;
  color: white;
  text-decoration: none;
}
/* Products */
.container-products {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  gap: 3rem;
}
.content-card-product {
  height: 150px;
  transform: translateY(-20px);
  border-radius: 20px 20px 0 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: var(--second-color);
  padding: 10px 10px;
}
.card-product {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  margin-bottom: -20px;
}

.container-img {
  position: relative;
  border: none;
  border-radius: 20px 20px 0px 0px;
  background: transparent;
}
/* .container-img:before {
  content: "";
  position: absolute;
  height: 200px;
  width: 100%;
  z-index: 1;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 20px 20px 0 0;
} */
.container-img img {
  width: 100%;
  display: block;
  background-size: cover;
  object-fit: cover;
  object-position: center;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  background: url("../../Logo.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.card-product:hover .discount {
  background-color: #000;
}

.caja {
  padding-left: 2rem;
  padding-right: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.center {
  display: flex;
  justify-content: center;
}
.center h1 {
  grid-row: 1/2;
  grid-column: 1/-1;
  font-family: "Bebas Neue", sans-serif;
  font-size: 15px;
  color: var(--primary-color);
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
  z-index: 0;
}
.stars {
  grid-row: 1/2;
  grid-column: 1/-1;
  font-family: "Bebas Neue", sans-serif;
  font-size: 10px;
  color: var(--primary-color);
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  z-index: 0;
}

.stars ion-icon {
  font-size: 1.3rem;
  color: var(--primary-color);
}
.pharra {
  color: var(--font-color);
}
.content-card-product h3 {
  grid-row: 2/3;
  grid-column: 1/-1;
  text-align: center;
  font-weight: bold;
  font-size: 2rem;
  margin-bottom: 1rem;
  cursor: pointer;
  font-family: "Abril Fatface", serif;
  color: var(--font-color);
}
.content-card-product .sub-cont {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}
.content-card-product .price {
  font-size: 1.2rem;
  font-weight: 600;
  color: rgb(206, 6, 6);
  margin: auto 10px;
}
.content-card-product ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.content-card-product a {
  border: solid var(--primary-color) 1px;
  height: max-content;
  width: max-content;
  border-radius: 50%;
}

.content-card-product ion-icon {
  font-size: 3rem;
  color: var(--primary-color);
  cursor: pointer;
}
.specials {
  display: flex;
  flex-direction: column;
  margin: 3rem 0px;
  background-color: var(--background-color);
  padding: 15px 5px;
}
.sabores li {
  font-size: 10px;
  color: var(--font2-color);
}
.sub-cont {
  padding-left: 2rem;
  padding-right: 2rem;
}
.navg {
  display: none;
  width: 600px;
  position: fixed;
  bottom: 0vh;
  z-index: 400;
  box-shadow: 0px -4px 6px rgba(0, 0, 0, 0.1);
}
.navigation {
  position: relative;
  width: 600px;
  height: 70px;
  background: var(--second-color);
  display: flex;
  justify-content: center;
  align-items: center;
}
.navigation ul {
  display: flex;
  justify-content: space-around;
  width: 100%;
  align-items: center;
}
.navigation ul li {
  position: relative;
  list-style: none;
  width: 70px;
  height: 70px;
  z-index: 100;
}
.navigation ul li a {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  text-align: center;
  font-weight: 500;
  text-decoration: none;
}
.navigation ul li a .icon {
  position: relative;
  display: block;
  line-height: 70px;
  font-size: 3rem;
  text-align: center;
  transition: 0.5s;
  color: var(--primary-color);
  z-index: 101;
}
.navigation ul li.active a .icon {
  transform: translateY(-30px);
  color: var(--second-color);
  font-size: 4rem;
}
.navigation ul li a .text {
  position: relative;
  color: var(--primary-color);
  font-weight: 400;
  font-size: 2rem;
  letter-spacing: 0.05em;
  transition: 0.5s;
  opacity: 0;
  transform: translateY(20px);
}
.navigation ul li.active a .text {
  opacity: 1;
  transform: translateY(-30px);
}
.indicator {
  position: absolute;
  top: -35px;
  transform: translateX(-225px);
  display: inline;
  width: 70px;
  height: 70px;
  background: var(--primary-color);
  border-radius: 50%;
  border: 6px solid var(--second-color);
  transition: 0.5s;
}
.navigation ul li:nth-child(1).active ~ .indicator {
  transform: translateX(-225px);
}
.navigation ul li:nth-child(2).active ~ .indicator {
  transform: translateX(-75px);
}
.navigation ul li:nth-child(3).active ~ .indicator {
  transform: translateX(75px);
}
.navigation ul li:nth-child(4).active ~ .indicator {
  transform: translateX(225px);
}

@media (max-width: 600px) {
  html {
    font-size: 42.5%;
  }
  .container-features {
    grid-template-columns: repeat(2, 1fr);
    padding: 3rem 2rem;
  }
  .barnav .derecha div h2 {
    font-size: 20px;
  }
  .barnav .derecha div h4 {
    font-size: 12px;
  }
  .barnav img {
    height: 40px;
  }
  .content-banner {
    max-width: 50rem;
    padding: 22rem 0;
  }
  .card-product:nth-child(5n):nth-last-child(odd),
  .card-product:nth-child(5n) {
    grid-column: span 2;
    margin-top: 0px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-radius: 20px;
    padding: 0px;
    margin-bottom: 0px;
  }
  .card-product:nth-child(5n):nth-last-child(odd) .container-img,
  .card-product:nth-child(5n) .container-img {
    position: relative;
    display: flex;
    justify-content: center;
    transform: translateY(0);
    width: 50%;
    z-index: auto;
  }

  .card-product:nth-child(5n):nth-last-child(odd) .container-img img,
  .card-product:nth-child(5n) .container-img img {
    position: relative;
    object-fit: cover;
    object-position: center;
    z-index: 100;
    border-radius: 20px 0px 0px 20px;
  }
  .card-product:nth-child(5n):nth-last-child(odd) .content-card-product,
  .card-product:nth-child(5n) .content-card-product {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 50%;
    height: 200px;
    padding: 0px 5px;
    transform: translateY(0);
    border-radius: 0px 20px 20px 0px;
    padding: 10px;
  }
  .card-product:nth-child(5n):nth-last-child(odd) .content-card-product h3,
  .card-product:nth-child(5n) .content-card-product h3 {
    grid-row: 2/3;
    grid-column: 1/-1;
    text-align: center;
    font-weight: bold;
    font-size: 4rem;
    margin-bottom: 1rem;
    cursor: pointer;
    font-family: "Abril Fatface", serif;
    color: var(--font-color);
  }
  .card-product:nth-child(5n):nth-last-child(odd)
    .content-card-product
    .sub-cont,
  .card-product:nth-child(5n) .content-card-product .sub-cont {
    margin-top: 10px;
    display: flex;
    justify-content: space-around;
  }
  .card-product:nth-child(5n):nth-last-child(odd) .content-card-product .price,
  .card-product:nth-child(5n) .content-card-product .price {
    font-size: 1.2rem;
    font-weight: 600;
    color: rgb(206, 6, 6);
    margin: auto 0px;
  }
  .card-product:nth-child(5n):nth-last-child(odd)
    .content-card-product
    .caja
    .space,
  .card-product:nth-child(5n) .content-card-product .caja .space {
    display: flex;
    flex-direction: column;
    margin-bottom: 5px;
    align-items: center;

    height: 65px;
  }
  .card-product:nth-child(5n):nth-last-child(odd) .content-card-product a,
  .card-product:nth-child(5n) .content-card-product a {
    border: solid var(--primary-color) 1px;
    height: max-content;
    width: max-content;
    border-radius: 50%;
  }

  .card-product:nth-child(5n):nth-last-child(odd)
    .content-card-product
    .star
    ion-icon,
  .card-product:nth-child(5n) .content-card-product .star ion-icon {
    font-size: 3rem;
    color: var(--primary-color);
    cursor: pointer;
  }

  .heading-1 {
    font-size: 2.8rem;
  }
  .container-img img {
    height: 200px;
  }
  #addIframe .container-img img {
    height: auto;
  }

  .card-feature {
    flex-direction: column;
    border-radius: 2rem;
  }

  .feature-content {
    align-items: center;
  }

  .feature-content p {
    font-size: 1.4rem;
    text-align: center;
  }

  .feature-content span {
    font-size: 1.6rem;
    text-align: center;
  }

  .container-products {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
    grid-auto-flow: dense;
    margin: 5px 4px;
  }

  .content-card-product h3 {
    font-size: 2.2rem;
  }

  .gallery {
    grid-template-rows: repeat(2, 20rem);
  }

  .contact-info {
    align-items: center;
  }

  .content p {
    font-size: 1.6rem;
  }
  .navg {
    width: 100%;
    left: 0;
  }
  .navigation {
    width: 100%;
  }
  .navigation ul li.active a .icon {
    font-size: 5rem;
  }
  .navigation ul li:nth-child(1).active ~ .indicator {
    transform: translateX(-37.5vw);
  }
  .navigation ul li:nth-child(2).active ~ .indicator {
    transform: translateX(-12.5vw);
  }
  .navigation ul li:nth-child(3).active ~ .indicator {
    transform: translateX(12.5vw);
  }
  .navigation ul li:nth-child(4).active ~ .indicator {
    transform: translateX(37.5vw);
  }
}
